<template>
    <header id="navbar" v-bind:style="{ top: barPos + 'px' }">
        <div id="nav-logo">Logo</div>
        <div id="nav-links">
            <a href="#">Home</a>
            <a href="#">Shop All</a>
            <a href="#">About Us</a>
        </div>
        <div id="nav-tools">
            <!--<input/>
            <div id="shopping-cart"/>-->
        </div>
    </header>
</template>

<script>
export default {
    name: 'NavBar',
    
    created() {
        /* For hiding and showing the navigation bar */
        window.addEventListener('scroll', this.onscroll);
    },
    
    unmounted() {
        window.removeEventListener('scroll', this.onscroll);
    },
    
    data() {
        return {
            lastPos: 0,
            barPos: 0,
        }
    },
    
    methods: {
        onscroll() {
            if(window.scrollY <= 70) {
                this.barPos = 0;
                return;
            }
            
            const scrollAmount = window.scrollY - this.lastPos;
            this.barPos -= scrollAmount;
            
            this.barPos = Math.min(0, Math.max(-70, this.barPos));
            
            this.lastPos = window.scrollY;
        }
    }
}
</script>

<style scoped>

#navbar
{
    position: fixed;
    
    top: 0;
    
    width: 100%;
    height: 70px;
    
    /* border-bottom: 1px solid var(--border-color); */
    
    background-color: white;
    
    box-shadow: 0 8px 20px var(--shadow-color);
    
    display: flex;
    
    z-index: 999;
}

#nav-links
{
    position: sticky;
    
    width: 100%;
    height: 100%;
    
    /* flex: 1 1 auto; */
    
    display: flex;
    justify-content: space-between;
    justify-items: center;
    
    background-color: yellow;
}

#nav-links > a
{
    color: var(--font-color);
    font-weight: bold;
    
    text-decoration: none;
}

#nav-logo
{
    width: 300px;
    background-color: red;
}

#nav-tools
{
    width: 300px;
    background-color: pink;
}

</style>